'use client';

import { useEffect, useRef, useState } from 'react';
import { Play, Pause } from 'lucide-react';

export default function AudioPlayer() {
  const audioRef = useRef<HTMLAudioElement>(null);
  const [isPlaying, setIsPlaying] = useState(false);

  useEffect(() => {
    const audio = audioRef.current;

    const handleInteraction = () => {
      if (audio && !isPlaying) {
        audio.play().then(() => setIsPlaying(true));
        document.removeEventListener('click', handleInteraction);
      }
    };

    document.addEventListener('click', handleInteraction);

    return () => {
      document.removeEventListener('click', handleInteraction);
    };
  }, [isPlaying]);

  const togglePlay = () => {
    const audio = audioRef.current;
    if (!audio) return;

    if (isPlaying) {
      audio.pause();
      setIsPlaying(false);
    } else {
      audio.play();
      setIsPlaying(true);
    }
  };

  return (
    <div className='fixed top-2 right-2 z-[9999]'>
      <audio ref={audioRef} src='/music/love_song.mp3' loop />
      <button
        onClick={togglePlay}
        className='bg-white border p-1 rounded-full shadow-md hover:bg-gray-100 transition-all'
        aria-label='Î∞∞Í≤Ω ÏùåÏïÖ Ïû¨ÏÉù/Ï†ïÏßÄ'
      >
        {isPlaying ? <Pause size={16} /> : <Play size={16} />}
      </button>
    </div>
  );
}
